/**
 * Updates a DeliveryProfile.
 *
 *
 * Each time the DeliveryProfile is updated,
 * `revision` increments by 1.
 * The current `revision` must be passed when updating the DeliveryProfile.
 * This ensures you're working with the latest DeliveryProfile
 * and prevents unintended overwrites.
 *
 * This method does not allow updating the DeliveryRegions in this DeliveryProfile.
 * Use AddDeliveryRegion, UpdateDeliveryRegion and RemoveDeliveryRegion for these functionalities.
 */
export function updateDeliveryProfile(
  payload: object
): RequestOptionsFactory<any> {
  function __updateDeliveryProfile({ host }: any) {
    const serializedData = transformPaths(payload, [
      {
        transformFn: transformSDKFieldMaskToRESTFieldMask,
        paths: [{ path: 'fieldMask' }],
      },
      {
        transformFn: transformSDKTimestampToRESTTimestamp,
        paths: [
          { path: 'deliveryProfile.createdDate' },
          { path: 'deliveryProfile.updatedDate' },
          { path: 'deliveryProfile.deliveryRegions.createdDate' },
        ],
      },
    ]);
    const metadata = {
      entityFqdn: 'wix.ecom.v1.delivery_profile',
      method: 'PATCH' as any,
      methodFqn:
        'wix.ecom.deliveryprofile.v1.DeliveryProfiles.UpdateDeliveryProfile',
      packageName: PACKAGE_NAME,
      url: resolveWixEcomDeliveryprofileV1DeliveryProfilesUrl({
        protoPath: '/v1/delivery-profiles/{deliveryProfile.id}',
        data: serializedData,
        host,
      }),
      data: serializedData,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'deliveryProfile.createdDate' },
              { path: 'deliveryProfile.updatedDate' },
              { path: 'deliveryProfile.deliveryRegions.createdDate' },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __updateDeliveryProfile;
}

